/* 
====== 
Variables 
======
*/
:root {
  --primaryLightColor: #d4e6a5;
  --primaryColor: #68b92e;
  --primaryDarkColor: #d45a38;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --mainBackground: #f1f5f8;
  --mainOverlay: rgba(58, 56, 58, 0.4);
  --mainBlack: #222;
  --mainGrey: #ececec;
  --darkGrey: #8b8a8a;
  --mainRed: #bd0303;
  --mainTransition: all 0.2s linear;
  --mainSpacing: 0.3rem;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
  --mainBorderRadius: 0.25rem;
  --smallWidth: 85vw;
  --maxWidth: 40rem;
  --fullWidth: 1170px;
}
/* 
====== 
Global Styles 
======
*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "DM Serif Display", serif;
  color: var(--mainBlack);
  background: var(--mainBackground);
  line-height: 1.4;
  font-size: 1rem;
  font-weight: 300;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--slantedFont);
  margin-bottom: 1.25rem;
  letter-spacing: var(--mainSpacing);
}
p {
  margin-bottom: 1.25rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: var(--mainBlack);
}
img {
  width: 100%;
  display: block;
}
.container {
  width: 60vw;
  height: 70vh;
  /* background-color: #8b8a8a; */
  text-align: center;
  margin-inline: auto;
  margin-block: 15vh;
  display: grid;
  grid-template-columns: repeat(3 1fr);
  grid-template-rows: repeat(2 1fr);
}
.heading > h1 {
  color: var(--darkGrey);
}
#session-label {
  align-self: center;
  justify-self: center;
  grid-row-start: 2;
  grid-row-end: 3;
}
#break-label {
  align-self: center;
  justify-self: center;
  grid-row-start: 2;
  grid-row-end: 3;
}
#timer {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
}
.heading {
  padding-top: 0.5rem;

  text-align: center;
  grid-column-start: 1;
  grid-column-end: 3;
}
.underline {
  margin-inline: auto;
  width: 10%;
  border-top: 2px solid black;
  margin-top: -1rem;
}
#session-label,
#break-label {
  text-align: center;
  width: 20vw;
}
.btn-container {
  height: 2.5rem;
  display: flex;
  justify-content: space-between;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  gap: 1rem;
  align-content: center;
}
.btn {
  background-color: transparent;
  border: none;
  font-size: 1.5rem;
}
#timer {
  margin-top: 1rem;
  padding-top: 1rem;
  padding-bottom: 0;
  border: 5px solid rgba(0, 0, 0, 0.363);
  border-radius: 1rem;
}
.timer-btns {
  margin-inline: auto;
  width: 30%;
  display: flex;
  justify-content: space-around;
}
